<template>
    <div>        
    <ABSListVuex
      :prop = "propList"
      :title = "data.QueryName"
      @rowClicked = "$parent.rowClicked"
      @rowDblClicked = "$parent.doDoubleClick"
      @rowLinkClick = "$parent.rowLink"
      @pageSize = "$parent.M_PageSize"
      @pagination = "$parent.M_Pagination"
      @filter = "$parent.M_Advance_Filter"
      @headTable = "$parent.M_Head_Table"
      @refreshColumn = "$parent.refreshColumn"
    />


        <div v-show="$parent.showForm"  :style="'margin-top:10px;'" class="el" mousetip mousetip-msg="I'm a tooltip">
            <div class="div-collapse" v-b-toggle.collapse1>
                <span class="master-span" v-show="IEBy.Input && IEBy.Edit">
                    Input By : {{IEBy.Input}} | Edit By : {{IEBy.Edit}} <font-awesome-icon icon="sort-down" class="icon-style-1__master" /> 
                </span>
            </div>
            <b-collapse id="collapse1" :visible="true">
                <b-row  style="padding-left: 10px; padding-bottom: 10px; !important;">     
					<b-col md="12" id="col-left" class="bColMasterForm">	
						<b-form :data-vv-scope="'FormScope_' + PageLevel + '_' + TabIndex" :data-vv-value-path="'FormScope_' + PageLevel + '_' + TabIndex">
                            <b-row style="padding-left:10px;">					
                                <b-col md="12" id="col-left"> <!-- table open -->
                                    <b-row>
                                      <b-col  md="6">
                                        <ABSinputTextVuex :prop="PI_paymentsequenceno" v-model="M_TN_TransactionTypeMappingProcess.paymentsequenceno"  ref="ref_paymentsequenceno"/>
                                      </b-col>
                                    </b-row>
                                    <b-row>
                                      <b-col  md="6">
                                        <ABSinputRadioButtonVuex @input="OnpaymentcontrolChange" :prop="PI_paymentcontrol" v-model="M_TN_TransactionTypeMappingProcess.paymentcontrol"  ref="ref_paymentcontrol" />
                                      </b-col>
                                    </b-row>
                                    <b-row>
                                      <b-col  md="6">
                                        <ABSTextAreaVuex @input="OnremarksChange" :prop="PI_remarks" v-model="M_TN_TransactionTypeMappingProcess.remarks"  ref="ref_remarks" />
                                      </b-col>
                                    </b-row>
                                </b-col> <!-- table close -->
                  
							</b-row>
							<ABSTAnalysis :prop="PTAnalysis" />
						</b-form>
					</b-col>
                </b-row>
            </b-collapse>
        </div>
    </div>
</template>

<script>

export default {
    props: {PageLevel:'', TabIndex: '', data: ''},
    data() {
        return {
			ValKey:null,
            FormType: "List",
			Module:"TN",
            propList: {
                initialWhere:"TransType = '" + this.ValKey + "'",
                LineNo: this.$parent.data.PageOrder,
                PageLevel: this.PageLevel,
                TabIndex: this.TabIndex, 
                OrderBy: '', 
                SourceField: '', 
                ParamView: '' 
            },
       
            IEBy: {Input: '', Edit: ''},   			

            M_TN_TransactionTypeMappingProcess :{
                paymentsequenceno: 0,
                trxtype: '',
                transtype: '',
                loistatus: '',
                lastatus: '',
                remarks: '',
                userinput: '',
                useredit: this.getDataUser().UserId,
                timeedit: '',
                timeinput: '',
                subportfoliocd: this.getDataUser().SubPortfolioCd,
                lastupdatestamp: 0,
                rowid: 0,
                paymentcontrol: 'L'
                    }
            ,
            PI_paymentsequenceno: { 
                cValidate :'max:8', 
                cName: 'paymentsequenceno', 
                cLabel: 'Payment Sequence', 
                cLabelSize: 4, 
                cOrder: 1, 
                cKey: false, 
                cType: 'text',
                cVisible: true, 
                cProtect: false, 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cParentForm: 'FormScope_' + this.PageLevel + '_' + this.TabIndex 
            }, 
            PI_paymentcontrol: { 
                cValidate :'', 
                cName: 'paymentcontrol', 
                cId: 'rdbpaymentcontrol', 
                cRadioOptions: [{ text: 'LOI', value: 'L' }, { text: 'LA', value: 'A' }], 
                cRadioDefaultOption: '', 
                cLabel:'Payment Control ', 
                cLabelSize: 4, 
                cOrder: 2, 
                cProtect: false, 
                cVisible:  true, 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cParentForm: 'FormScope_' + this.PageLevel + '_' + this.TabIndex 
            }, 
            PI_remarks: { 
                cValidate :'', 
                cName:'remarks', 
                cLabel:'Remarks', 
                cLabelSize: 4, 
                cOrder:3, 
                cKey: false, 
                cDefault: '', 
                cProtect: false, 
                cVisible: true, 
                cResize: false, 
                cReadOnly: false, 
                cRows: 3, 
                cMaxRows: 3, 
                cSize: 'md', 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cParentForm: 'FormScope_' + this.PageLevel + '_' + this.TabIndex, 
            }, 

			PTAnalysis: {
                cPageLevel: this.PageLevel,
                cTabIndex: this.TabIndex,
                cPageMasterSeq: this.data.PageMasterSeq,
                cFormPageNo: this.data.PageOrder
            }         
        }
    },
	computed : {
    inputStatus() {
      if(this.$store.getters.getLevel == this.PageLevel && this.$store.getters.getTab == this.TabIndex){
        return this.$store.getters.getStatus.toUpperCase()
      }
      else {
        return 'VIEW'
      }
    },
	DataRow(){
		return this.$store.getters.GetPage2Data
	},
    DataRowPage1(){
		return this.$store.getters.GetPage1Data
	}
  },
    methods: {
        OnpaymentcontrolChange (data) {
        this.$nextTick(() => {
            if (this.inputStatus != "VIEW") {
//{paymentcontrol}
            }
        })
        this.$forceUpdate()
        },
        OnremarksChange (data) {
        this.$nextTick(() => {
            if (this.inputStatus != "VIEW") {
//{remarks}
            }
        })
        this.$forceUpdate()
        },
		
		setToolbarButton () {
           //this.getToolbar().statusFunction[4].disabled = false
		},
        M_Head_Table () {
        },
        M_PageSize(){
        },
        M_TabClick(){
        },
        M_Pagination(){
        },
        M_Advance_Filter(){
        },
		M_Search(data){
        },
        M_Refresh(){
        },
		M_Cancel() {			
		},		
		doDoubleClick(){
        },
		rowClicked (record, index) {
        },
        rowLink: function(url){
            this.$refs.modalTest.show()
        },
		paramFromParent(){
            this.$parent.showForm = false
			
            let data = this.$store.getters.GetPage1Data
            this.M_TN_TransactionTypeMappingProcess.trxtype = data.TrxType 
            this.propList.initialWhere =" TrxType = '" + data.TrxType + "' "
        

            this.FormToABSList().doGetList('','eb_getList')
        },		       
        M_Insert() {
            if (this.M_TN_TransactionTypeMappingProcess.paymentcontrol == 'L'){
               this.M_TN_TransactionTypeMappingProcess.loistatus = 'Y'
               this.M_TN_TransactionTypeMappingProcess.lastatus = 'N'
            }
            else if (this.M_TN_TransactionTypeMappingProcess.paymentcontrol == 'A'){
               this.M_TN_TransactionTypeMappingProcess.loistatus = 'N'
               this.M_TN_TransactionTypeMappingProcess.lastatus = 'Y'
            }

            var param = {			
                OptionSeq: this.getOptionSeq().OptionSeq,
                LineNo: this.$parent.data.PageOrder,
                SubPortfoliocd: this.getDataUser().SubPortfolioCd,
                TransType: this.M_TN_TransactionTypeMappingProcess.trxtype,
                PaymentSequenceNo: this.M_TN_TransactionTypeMappingProcess.paymentsequenceno,
                LOIStatus: this.M_TN_TransactionTypeMappingProcess.loistatus,
                LAStatus: this.M_TN_TransactionTypeMappingProcess.lastatus,
                Remarks: this.M_TN_TransactionTypeMappingProcess.remarks,
                UserInput: this.getDataUser().UserId                 
            }

            this.postJSON(this.getUrlInsert(), param)
            .then((response) => {
                if(response == null) return

				//kalo ada hit api lagi,tolong script dibawah ini dipindahkan di dalam then'a ,setelah itu hapus comment ini
                this.$parent.resultInsert(response.Message)
					
				
            })
        },
        M_Update() {
            if (this.M_TN_TransactionTypeMappingProcess.paymentcontrol == 'L'){
               this.M_TN_TransactionTypeMappingProcess.loistatus = 'Y'
               this.M_TN_TransactionTypeMappingProcess.lastatus = 'N'
            }
            else if (this.M_TN_TransactionTypeMappingProcess.paymentcontrol == 'A'){
               this.M_TN_TransactionTypeMappingProcess.loistatus = 'N'
               this.M_TN_TransactionTypeMappingProcess.lastatus = 'Y'
            }
            var param = {			
                OptionSeq: this.getOptionSeq().OptionSeq,
                LineNo: this.$parent.data.PageOrder,
                SubPortfoliocd: this.getDataUser().SubPortfolioCd,
                TransType: this.M_TN_TransactionTypeMappingProcess.trxtype,
                LOIStatus: this.M_TN_TransactionTypeMappingProcess.loistatus,
                LAStatus: this.M_TN_TransactionTypeMappingProcess.lastatus,
                Remarks: this.M_TN_TransactionTypeMappingProcess.remarks,
                UserEdit: this.getDataUser().UserId ,
                LastUpdateStamp: this.M_TN_TransactionTypeMappingProcess.lastupdatestamp                
            }

            this.postJSON(this.getUrlUpdate(), param)
            .then((response) => {
                if(response == null) return

                //kalo ada hit api lagi,tolong script dibawah ini dipindahkan di dalam then'a ,setelah itu hapus comment ini
                this.$parent.resultUpdate(response.Message)
				
            })
        },
		M_ClearForm(){
            this.M_TN_TransactionTypeMappingProcess = {
                paymentsequenceno: 0,
                trxtype: '',
                loistatus: '',
                lastatus: '',
                remarks: '',
                userinput: '',
                useredit: this.getDataUser().UserId,
                timeedit: '',
                timeinput: '',
                subportfoliocd: this.getDataUser().SubPortfolioCd,
                lastupdatestamp: 0,
                rowid: 0,
                paymentcontrol: 'L'
                    }
            		
		},
        M_New(){
            let data = this.$store.getters.GetPage1Data
            this.M_TN_TransactionTypeMappingProcess.trxtype = data.TrxType 



        },
        M_Edit(){
        },
        M_Delete(dt){           

            var data = this.FormToABSList().getRowSelected()  
        var dataDelete = []

        
        data.forEach((value) => {
          dataDelete.push({
          TransType: value.trxtype,
          PaymentSequenceNo: value.PaymentSequenceNo,
          LastUpdateStamp: value.LastUpdateStamp,
          _Message_:''
                  })
        })
        
        var param = {
                  OptionSeq: this.getOptionSeq().OptionSeq,
                  LineNo: this.$parent.data.PageOrder,
                  Data: dataDelete
              }
              
              this.postJSONMulti( this.getUrlNewDeleteMulti(), param )
              .then(response => {
                  if (response == null) return

                  this.$parent.resultDelete()
              })
        },        
        getDataBy (record) {
            // console.log(record)
            var param = {
                OptionSeq: this.getOptionSeq().OptionSeq,
                LineNo: this.$parent.data.PageOrder
,               paymentsequentno: record.PaymentSequenceNo,
                transtype: record.trxtype,
                SubPortfolioCd: this.getDataUser().SubPortfolioCd				
            }

            this.postEncode( this.getUrlById(), param )
            .then(response => {
                if(response == null) return

                var data = response.Data[0]
                // var paymentctrl = []
                var paymentctrl = ''
                if (data.loistatus == 'Y'){
                    paymentctrl = 'L'
                    // paymentctrl.push('L')
                } 
                if (data.lastatus == 'Y'){
                    paymentctrl = 'A'
                    // paymentctrl.push('A')
                }
this.M_TN_TransactionTypeMappingProcess = {
                paymentsequenceno: this.isCurrency(record.PaymentSequenceNo, this.decimal),
                transtype: data.trxtype,
                trxtype: data.trxtype,
                loistatus: data.loistatus,
                lastatus: data.lastatus,
                remarks: data.remarks,
                userinput: data.userinput,
                useredit: data.useredit,
                timeedit: data.timeedit,
                timeinput: data.timeinput,
                subportfoliocd: data.subportfoliocd,
                lastupdatestamp: record.LastUpdateStamp,
                rowid: this.isCurrency(data.rowid, this.decimal),
                paymentcontrol: paymentctrl
                    }
                 

  

                this.IEBy.Input = data.userinput
                this.IEBy.Edit = data.useredit

            })
        }
		
    },
    created: function() {
    },
    beforeMount: function() {
    },
    mounted: function() {
        this.hideSideBarMenu()
		
    },
    beforeUpdate: function() {
    },
    updated: function() {
    },
    beforeDestroy: function() {
    },
    destroyed: function() {
    }
}
</script>

